import "./projects-header.css";

import {Link} from "react-router-dom";

export default function Header() {
    return <div className='projects-header'>
        <div className='overlay-projects-header'>
            <div className='h1-and-navbar-projects'>
                <h1>Hi, I am Tiberiu</h1>
                <nav className='nav-bar-projects'>
                    <Link to="/">HOME</Link>
                    <Link to="/about">ABOUT</Link>
                    <Link to="/portfolio">PORTFOLIO</Link>
                    <Link to="/contact">CONTACT</Link>
                </nav>
            </div>
            <h1 className='my-projects'>MY PORTFOLIO</h1>
        </div>
    </div>
}
    